const webpack = require('webpack');

const vendors = [
    'react',
    'react-dom',
    'react-highcharts',
    'react-bootstrap',
    'highcharts',
    'highcharts/highstock.js',
    'react-highcharts/dist/ReactHighstock.js',
    //'antd',
    //'antd/dist/antd.css',
    'css-loader/lib/css-base.js',
    'style-loader/addStyles.js',
    'babel-polyfill',
    'jquery'
];

module.exports = {
    output: {
        path: 'public/js',
        filename: '[name].js',
        library: '[name]',
    },
    entry: {
        "vendor": vendors,
    },
    module: {
        loaders: [{
            test: /\.css$/,
            loader: 'style!css'
        }, ]
    },
    plugins: [
        new webpack.optimize.UglifyJsPlugin({
            output: {
                comments: false,
            },
            compress: {
                warnings: false
            }
        }),
        new webpack.optimize.OccurenceOrderPlugin(),
        new webpack.optimize.DedupePlugin(),
        new webpack.DefinePlugin({
            'process.env': {
                NODE_ENV: JSON.stringify('production'),
            },
        }),

        new webpack.DllPlugin({
            path: 'manifest.json',
            name: '[name]',
            context: __dirname,
        }),

    ],
};



